/* eslint-disable */

export default (el) => {
    var imageContent = el.querySelector('.image-content');
    var videoContent = el.querySelector('.video-content');

    imageContent.addEventListener('click', function (event) {
        let videoId = videoContent.getAttribute('data-video-id');
        let html = '<iframe src="https://www.youtube.com/embed/' + videoId + '?autoplay=1&playsinline=1" frameborder="0" allowfullscreen></iframe>';
        videoContent.innerHTML = html;
        videoContent.classList.add('open');
    }, false);

};
